
.page-wrap{text-align:center;font-size:18px;margin-top:180px
}
.page-wrap ul{display:inline-block;list-style:none;overflow:hidden
}
.page-wrap ul li{float:left;color:#1e5a6b;padding:1px 10px;margin:0 5px;border-radius:50%;-webkit-user-select:none;-moz-user-select:none;-ms-user-select:none;user-select:none;border:1px solid transparent
}
.page-wrap .pointer{cursor:pointer
}
.page-wrap .hover:hover{border-color:#7ba6b3
}
.page-wrap .li-page{line-height:1.5;cursor:pointer;color:#1e5a6b
}
.page-wrap .li-page:hover{color:#7ba6b3
}
.page-wrap .active{border-color:#246c81
}


