
*{margin:0;padding:0;box-sizing:border-box
}
a{text-decoration:none;color:inherit
}
#app{font-family:'Avenir',Helvetica,Arial,sans-serif;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale
}
#evanyou{position:fixed;width:100%;height:100%;top:0;left:0;z-index:-10
}
.github-corner svg{fill:#7ba6b3;color:#fff;position:absolute;top:0;right:0;border:0
}
.github-corner:hover .octo-arm{animation:octocat-wave 560ms ease-in-out
}
@keyframes octocat-wave{
0%,100%{transform:rotate(0)
}
20%,60%{transform:rotate(-25deg)
}
40%,80%{transform:rotate(10deg)
}
}
@media (max-width:500px){
.github-corner .github-corner:hover .octo-arm{animation:none
}
.github-corner .github-corner .octo-arm{animation:octocat-wave 560ms ease-in-out
}
}


